import React, { Component } from "react";

class AddToCartComponent extends Component {
  state = {
    quantity: "",
    img: "",
    name: "",
    desc: "",
    price: "",
    weight: ""
  };

  handleSubmit = e => {
    e.preventDefault(); 
    this.props.addProduct(this.state); 
  };

  render() {
    return (
      <div>
        <form onSubmit={this.handleSubmit}>
          <button>add to carttt</button>
           
        </form>
      </div>
    );
  }
}

export default AddToCartComponent;
